let channel = Configuration.channel;
  let appHost = "127.0.0.1";
  let appPort = "9101";
  let protocol = "ws";
  let wsUrl = `${protocol}://${appHost}:${appPort}`;
  let ws; // = new WebSocket(wsUrl);

newSocketConnection();

function updateReactiveModelVariable( key, value){
    reactiveModel.value[key] = value;
    window.sharedData.setExternalData(reactiveModel.value);
}

function sendWsMessage( message, payload={} ){
  let msg = JSON.stringify({
        'channel': channel,
        'message': message,
        'payload': payload
      });
      ws.send( msg );
}

function newSocketConnection() {
  ws = new WebSocket(wsUrl);

    ws.addEventListener('open', event => {
      sendWsMessage( 'subscribe' );      
    });

    ws.addEventListener('message', event => {
      if( event.data == 'Subscription: OK' ){
        sendWsMessage( 'watchers', 
        { 
          payload:{
            "field": "isready",
            "newval": true,
            "oldval": false,
          }
        });
        return;
      }else if( event.data.indexOf('{') == 0  ){
        let msgObject = JSON.parse( event.data );
        if( msgObject.key != null ){
          updateReactiveModelVariable( msgObject.key, msgObject.value );
        }
      }
    });

    ws.addEventListener('close', event => {
      console.log( 'newSocketConnection::close', event );
    });

    ws.addEventListener('error', error => {
      console.log( 'newSocketConnection::error', error );
    });

    return ws
}

