            this.vueApp = new Vue({
                el: '#vueinternal_'+uniqueId,
                //components: { QBtn:Quasar.}, 
                data: function(){ 
                    
                    return {                        
                        message:"TESTING_ID",
                        attributes: mappedAttributes,  
                        serverData: serverData,
                    }
                }, 
                computed:{
                  parsedAttributes(){
                    let result = {};
                    let attributesObject = this.attributes;
                    console.log( 'parsedAttributes', attributesObject );
                    for( let keyRaw in attributesObject ){
                      let key = keyRaw[0] == ':' ? keyRaw.substring(1) : keyRaw;
                      let propertyDefinition = componentDefinition.properties.find( prop => prop.name == key );
                      let attributeValue = attributesObject[keyRaw];
                      let valueIsBinding = modelFieldsByName[attributeValue] != null;
                      if( key == 'v-model' ){
                        result['v-model'] = this.serverData[attributeValue];
                        continue;
                      }
                      if( propertyDefinition ){
                        if( valueIsBinding ){
                          result[`:${key}`] = this.serverData[attributeValue];
                        }else{
                          if( propertyDefinition.type == "String" ){
                            result[`${key}`] = attributeValue;
                          }else{
                            result[`:${key}`] = attributeValue;
                          }
                        }
                      }else{
                        result[`${key}`] = attributeValue;
                      }
                    }
                    return result;
                  }
                },
                methods: {
                    updateServerData(newServerData){
                        this.serverData = newServerData;
                    }, 
                    updateAttributes(attrs){             
                        this.attributes = getMappedAttributes(attrs);
                        mappedAttributes = this.attributes;
                        this.updateServerData(this.serverData);
                        this.$forceUpdate();
                    }, 

                }, 
                mounted() {
                }
            });

