<template>   
    <div class="row propRow" v-for="property in definitionsListWithValues" :key="property.id">
        <div class="col-5">{{ property.label }}</div>
        <div class="col-7" v-if="property.id=='x' || property.id=='y' || property.id=='z'">
            <vscode-dropdown style="width: 100%;" name="" id="" v-model="property.valueHolderParent[property.propName]" @change="submitChanges">
                <vscode-option :value="'$_{'+dataSource+'.'+field+'}'" v-for="field in dataSourceColumns">{{ field }}</vscode-option>
            </vscode-dropdown>
        </div>
        <div class="col-7" v-else-if="property.type=='enum'">
            <vscode-dropdown style="width: 100%;" name="" id="" v-model="property.valueHolderParent[property.propName]" @change="submitChanges">
                <vscode-option :value="field" v-for="field in property.options">{{ field }}</vscode-option>
            </vscode-dropdown>
        </div>
        <div class="col-7" v-else style="align-items: center; position: relative; display: flex;">          
            <input type="checkbox" v-if="property.type=='checkbox'" name="" id="" v-model="property.valueHolderParent[property.propName]" @change="submitChanges">
            <input v-if="property.type=='color'" :type="property.type" v-model="property.valueHolderParent[property.propName]" @change="submitChanges" style="height: 23px; width: 23px; margin-right: 5px; background-color:#ffffff00">
            <vscode-text-field v-if="property.type=='color'" type="text" name="" id="" v-model="property.valueHolderParent[property.propName]" @change="submitChanges" style="width:80px;"></vscode-text-field>
            <vscode-text-field v-if="property.type=='string' || property.type=='number'" :type="property.type" name="" id="" v-model="property.valueHolderParent[property.propName]" @change="submitChanges"></vscode-text-field>
        </div>
    </div>
</template>
  
<script setup>
  import { ref, computed } from 'vue';

  // PROPS and EMITS
  const props = defineProps(['dataSource', 'dataSourceColumns', 'treeObject', 'propertiesDefinition'])
  const emit = defineEmits(['change'])



  /*  ======================================================================================
                                    COMPONENT METHODS
    ====================================================================================== */
  function submitChanges(){
    emit('change', props.treeObject);
  }

  function getNestedPropertyParent(obj, propName) {
    const propParts = propName.split('.');
    if( propParts.length == 1 )
      return obj;

    for (let i = 0; i < propParts.length-1; i++) {
        if (!obj || !obj.hasOwnProperty(propParts[i])) {
            return undefined;
        }
        obj = obj[propParts[i]];
    }
    return obj;
}

function getNestedProperty(obj, propName) {
    const propParts = propName.split('.');
    for (let i = 0; i < propParts.length; i++) {
        if (!obj || !obj.hasOwnProperty(propParts[i])) {
            return undefined;
        }
        obj = obj[propParts[i]];
    }
    return obj;
}

const definitionsListWithValues = computed( ()=>{
    let result = [];
    for( let i = 0; i < props.propertiesDefinition.length; i++ ){
        let propertyDefCloned = { ...props.propertiesDefinition[i] };
        let value = getNestedProperty( props.treeObject, propertyDefCloned.id );
        if( value !== undefined ){
            propertyDefCloned.valueHolderParent = getNestedPropertyParent( props.treeObject, propertyDefCloned.id );
            propertyDefCloned.propName = propertyDefCloned.id.split('.').pop();
            result.push( propertyDefCloned );
        }
    }
    return result;
});
  
</script>
  

<style>
.propRow {
  margin-bottom: 5px;
}

/* generic grid */
.row::after {
  content: "";
  clear: both;
  display: table;
}

/* Basic row class */
.row {
  margin-left: auto;
  margin-right: auto;
  width: 100%;
  display: flex;
}

/* Column class */
.col {
  float: left;
  padding: 15px;
  box-sizing: border-box;
}

/* 12-column classes */
.col-1 { width: 8.33%; }
.col-2 { width: 16.66%; }
.col-3 { width: 25%; }
.col-4 { width: 33.33%; }
.col-5 { width: 41.66%; }
.col-6 { width: 50%; }
.col-7 { width: 58.33%; }
.col-8 { width: 66.66%; }
.col-9 { width: 75%; }
.col-10 { width: 83.33%; }
.col-11 { width: 91.66%; }
.col-12 { width: 100%; }

</style>