<template>
    <div class="chartConfigContainer" v-if="selectedComponent && selectedComponent.attributes.type=='plotly'">
        <div class="tabSelector">
            <div @click="currentTabIndex=tab.id" :class="{ tabItem:true, tabItemSelected:tab.id==currentTabIndex }" v-for="tab in tabsList" :key="tab">{{ tab.name }}</div>
            
        </div>
        
        <!-- ---------------------------------------------------
                            DATA (TRACES)
        --------------------------------------------------- -->
        <div class="tabSectionContainer" v-if="currentTabIndex==0">
          <div style="padding:20px 0px;">
            <div style="padding:0px 0px 10px;"><b>Reactive Data</b></div>
            <vscode-dropdown name="" id="" v-model="selectedDataSource">
                <vscode-option :value="null">None</vscode-option>
                <vscode-option :value="field.name" v-for="field in dataSourceFields">{{ field.name }}</vscode-option>
            </vscode-dropdown><br/>
          </div>
          <div class="tracesContainer" v-if="isSelectedDataSourceConfigurable()">
            
            <div class="foldableGroup" v-for="trace in traces" :key="trace">
                <div class="foldableGroupHeader">
                    <div style="width:40px;">?</div>
                    <div style="width:50px;flex-grow: 1">{{ trace.name }}</div>
                    <!-- <div style="">v</div> -->
                </div>
                <div class="foldableGroupContent">
                    <div class="subgroup">
                        
                        <div class="subgroupContent">
                            <ChartPropertiesSection 
                            :dataSource="selectedDataSource" :treeObject="trace" 
                            :dataSourceColumns="getSelectedDataSourceColumns"
                            :propertiesDefinition="tracePropertiesDefinition" @change="dataChanged"/>                            
                        </div>
                    </div>
                    <div class="addPropertyButtonRow">
                      <button class="addPropertyButton" v-if="!addPropertyDestination" @click="showAddPropertyList(trace)">+</button>
                    </div>
                  <AddPropertyDialog v-if="addPropertyDestination==trace" :defObject="tracePropertiesDefinition" :destination="addPropertyDestination" :propslist="availableDataProperties(tracePropertiesDefinition, trace)" @propAdded="addProperty" @close="hideAddProperty">
                  </AddPropertyDialog>
                  
                
              </div>
            </div>
          </div>
          
          <div v-if="selectedDataSource && isSelectedDataSourceConfigurable()" style="border-top: solid 1px var(--badge-background); padding: 20px;     text-align: center;">
            <vscode-button class="addTraceButton" @click="addNewTrace">+ Trace</vscode-button>
          </div>
        </div>

        <!-- ---------------------------------------------------
                              LAYOUT
        --------------------------------------------------- -->
        <div class="tabSectionContainer" v-if="currentTabIndex==1">
          <div class="subgroup">
              
              <div class="subgroupContent">
                  <ChartPropertiesSection 
                  :dataSource="selectedDataSource" :treeObject="layout" 
                  :dataSourceColumns="getSelectedDataSourceColumns"
                  :propertiesDefinition="layoutPropertiesDefinition" 
                  @change="layoutChanged"/>                            
              </div>
          </div>
          <div class="addPropertyButtonRow">
            <button class="addPropertyButton" v-if="!addPropertyDestination" @click="showAddPropertyList(layout)">+</button>
          </div>
          <AddPropertyDialog v-if="addPropertyDestination==layout" :defObject="layoutPropertiesDefinition" :destination="layout" :propslist="availableDataProperties(layoutPropertiesDefinition, layout)" @propAdded="addProperty" @close="hideAddProperty">
          </AddPropertyDialog>
        </div>

        <!-- ---------------------------------------------------
                              CONFIG
        --------------------------------------------------- -->
        <div class="tabSectionContainer" v-if="currentTabIndex==2">
          <div class="subgroup">
              
              <div class="subgroupContent">
                  <ChartPropertiesSection 
                  :dataSource="selectedDataSource" :treeObject="config" 
                  :dataSourceColumns="getSelectedDataSourceColumns"
                  :propertiesDefinition="configPropertiesDefinition" 
                  @change="configChanged"/>                            
              </div>
          </div>
          <div class="addPropertyButtonRow">
            <button class="addPropertyButton" v-if="!addPropertyDestination" @click="showAddPropertyList(config)">+</button>
          </div>
          <AddPropertyDialog v-if="addPropertyDestination==config" :defObject="configPropertiesDefinition" :destination="config" :propslist="availableDataProperties(configPropertiesDefinition, config)" @propAdded="addProperty" @close="hideAddProperty">
          </AddPropertyDialog>
            
        </div>

    </div>
</template>

<script setup>
import { ref, onMounted, onBeforeMount, computed } from 'vue'
import AddPropertyDialog from './panels/properties/AddPropertyDialog.vue';
import ChartPropertiesSection from './ChartPropertiesSection.vue';

// ====================== PROPS ====================== 

const props = defineProps(['appData', 'selectedComponent']);



// ====================== DATA ====================== 

let currentTabIndex = ref(0);
let tabsList = ref( [ {name:"Data", id:0}, {name:"Layout", id:1}, {name:"Config", id:2} ] );

let dataSourceFields = ref(null);
let selectedDataSource = ref(null);

let showAddConfigProperty = ref(false);
let addPropertyDestination = ref(null);

let traces = ref([]);
let config = ref([]);
let layout = ref([]);
let modelFields = ref(props.appData.pages[0].model.fields);
let modelFieldsDict = {};
modelFields.value.forEach( (field)=>{
    modelFieldsDict[field.name] = field;
})





let tracePropertiesDefinition = ref([
  { id:"name", label:"Name", type: "string", default: "" },
  { id:"type", label:"Type", type: "enum", default: "scatter", options:["scatter","bar","histogram"] },
  { id:"x", label:"X", type: "array", default: [0,1,2,3,4,5] },
  { id:"y", label:"Y", type: "array", default: [2,4,8,16,32] },
  { id:"marker.size", label:"Marker Size", type: "number", default: 5 },
  { id:"marker.color", label:"Marker Color", type: "color", default: '#2578B4' },
]);

let configPropertiesDefinition = ref([
  { id:"autosizable", label:"Autosizable", type: "checkbox", default: false },
  { id:"displaylogo", label:"Display Logo", type: "checkbox", default: true },
  { id:"scrollZoom", label:"Scroll Zoom", type: "checkbox", default: true },
  { id:"editable", label:"Editable", type: "checkbox", default: true },
  { id:"displayModeBar", label:"Mode Bar", type: "checkbox", default: true },
]);
let layoutPropertiesDefinition = ref([
  { id:"title", label:"Title", type: "string", default: "Hello Def" },
  { id:"showlegend", label:"Show Legend", type: "checkbox", default: true },
  { id:"paper_bgcolor", label:"Paper", type: "color", default: "#ffffff" },
  { id:"plot_bgcolor", label:"Background", type: "color", default: "#ffffff" },
  { id:"font.color", label:"Font Color", type: "color", default: "#000000" },
  { id:"font.size", label:"Font Size", type: "number", default: 10 },
]);


// ====================== COMPUTED ====================== 

const getSelectedDataSourceColumns = computed(() => {
  let selectedDataSourceColumns = [];
  let selectedDataSourceName = selectedDataSource.value;
  if( selectedDataSourceName ){
    let selectedDataSourceField = dataSourceFields.value.find( (field)=>{ return field.name == selectedDataSourceName } );
    if( selectedDataSourceField ){
      selectedDataSourceColumns = selectedDataSourceField.columns;
    }
  }
  return selectedDataSourceColumns;
})

function availableDataProperties(defObject, destination) {
  let notUsedProperties = defObject.filter( (propDef)=>{
    let isUsed = false;
    let existingProperty = getNestedProperty(destination, propDef.id);
    if( existingProperty != null ){
      isUsed = true;
    }    
    return !isUsed;
  })
  return notUsedProperties;
};

const availableConfigProperties = computed(() => {
  let notUsedProperties = configPropertiesDefinition.value.filter( (propDef)=>{
    let isUsed = false;
    let existingProperty = getNestedProperty(addPropertyDestination.value, propDef.id);
    if( existingProperty != null ){
      isUsed = true;
    }    
    return !isUsed;
  })
  return notUsedProperties;
})





// ====================== Methods ====================== 

function layoutChanged(newTreeObject){
  submitChanges();
}
function configChanged(newTreeObject){
  submitChanges();
}

function dataChanged(newTreeObject){
  submitChanges();
}

function showAddPropertyList(destination){
    addPropertyDestination.value = destination;
}

function hideAddProperty(){
    addPropertyDestination.value = null;
}

function addProperty(prop, defObject, destination){
  let propId = prop.id;
  let propDef = defObject.find( (propDef)=>{ return propDef.id == propId } );
  //addPropertyDestination.value = destination;
  setNestedProperty(destination, propId, propDef.default);
    submitChanges();
}

function getNestedProperty(obj, propName) {
    const propParts = propName.split('.');
    for (let i = 0; i < propParts.length; i++) {
        if (!obj || !obj.hasOwnProperty(propParts[i])) {
            return undefined;
        }
        obj = obj[propParts[i]];
    }
    return obj;
}

function setNestedProperty(obj, propName, value) {
    const propParts = propName.split('.');
    for (let i = 0; i < propParts.length - 1; i++) {
        if (!obj.hasOwnProperty(propParts[i]) || typeof obj[propParts[i]] !== 'object') {
            obj[propParts[i]] = {};
        }
        obj = obj[propParts[i]];
    }
    obj[propParts[propParts.length - 1]] = value;
}



function isSelectedDataSourceConfigurable(){
  let dsField = props.appData.pages[0].model.fields.find( (field)=>{ 
    return field.name == selectedDataSource.value; //"DataFrames.DataFrame" || field.type == "PlotlyData" 
  } );
  if( !dsField )
    return false;

  if(dsField.type == "DataFrames.DataFrame")
    return true;

  return false;
}

function getFieldsOfDataFrameType(){
  let dsFields = props.appData.pages[0].model.fields.filter( (field)=>{ 
    return field.type == "DataFrames.DataFrame" || field.type == "PlotlyData" 
  } );
  dataSourceFields.value = dsFields;
}



function submitChanges(){
  let newData = btoa( JSON.stringify(traces.value) );
  let newConfig = btoa( JSON.stringify(config.value) );
  let newLayout = btoa( JSON.stringify(layout.value) );
  props.selectedComponent.setAttributes({data:newData, config: newConfig, layout:newLayout })
  props.selectedComponent.view.$el[0].vueApp.updateAttributes({data:newData, config: newConfig, layout:newLayout });
  //props.selectedComponent.view.$el[0].vueApp.$forceUpdate();

}

  
  function getDataSourceFromTraces(){
    let dataSourceProperties = [];
    traces.value.forEach( (trace)=>{
      let bindingsInTrace = findBindingValues(trace);
      bindingsInTrace.forEach( (binding)=>{
        let bindingName = binding.value.replace('$_{', '').replace('}', '');
        if( !dataSourceProperties.includes(bindingName) ){
          dataSourceProperties.push(bindingName);
        }
      })      
    } );
    dataSourceProperties = dataSourceProperties.map( (propName)=>{
      return propName.split(".")[0]
    } );
    selectedDataSource.value = dataSourceProperties[0];

  }

  
function setSelectedComponent(newComponent){
  //let newComponent = props.selectedComponent;
    if( !newComponent || newComponent.attributes.type != 'plotly' ){
      traces.value = null;
      config.value = null;
      layout.value = null;
      return;
    }
    let componentAttributesRaw = newComponent.getAttributes();
    if( componentAttributesRaw.gb_c__data )
      componentAttributesRaw.gb_c__data = atob(componentAttributesRaw.gb_c__data);
    if( componentAttributesRaw.gb_c__config )
      componentAttributesRaw.gb_c__config = atob(componentAttributesRaw.gb_c__config);
    if( componentAttributesRaw.gb_c__layout )
      componentAttributesRaw.gb_c__layout = atob(componentAttributesRaw.gb_c__layout);
    

    let data = componentAttributesRaw.gb_c__data || "[]";
    data = JSON.parse(data);
    traces.value = data;

    let configInput = componentAttributesRaw.gb_c__config || "{}";
    configInput = JSON.parse(configInput);
    config.value = configInput;
    
    let layoutInput = componentAttributesRaw.gb_c__layout || "{}";
    layoutInput = JSON.parse(layoutInput);
    layout.value = layoutInput;
    getDataSourceFromTraces();
}

function findBindingValues(obj) {
  let result = [];

  function recurse(o) {
    for (let key in o) {
      if (typeof o[key] === 'object' && o[key] !== null) {
        recurse(o[key]);
      } else if (typeof o[key] === 'string' && o[key].startsWith('$_{') && o[key].endsWith('}')) {
        result.push({ value: o[key] });
      }
    }
  }

  recurse(obj);
  return result;
}

function addNewTrace(){
  traces.value.push( { x:'', y:'', type:'scatter', name:'Chg 1' } )
}

function assignNewDataSource(dsName){
  traces.value = [
    { x:'$_{columnB}', y:'$_{columnA}', type:'scatter', name:'Chg 1' }, 
    { x:'$_{columnB}', y:'$_{columnC}', type:'scatter', name:'Chg 2' }, 
  ];
  submitChanges();

}
onBeforeMount( ()=>{
    setSelectedComponent( props.selectedComponent );
    getFieldsOfDataFrameType();
});

defineExpose({
    setSelectedComponent, assignNewDataSource
})

</script>




