<template>
  <div class="mainContainer" v-if="appData">
    <div class="topBar">
      <div style="width: 100%;">
        <!-- <img src="img/logo.png" alt="logo" style="height: 100%;"/> -->
      </div>
      <div class="topToolBar">
        <div>
          <vscode-button @click="saveTemplate">Save</vscode-button>
        </div>
      </div>
    </div>
    <div class="rowStack">
      <div class="centralBar">
        <div class="panel canvasPanel" style="">
          <Panel :hideTitle="true" title="Builder" style="height: 100%; display: flex; flex-direction: column;">
            <CanvasPanel ref="canvasPanel" @componentSelected="onComponentSelected" @bindingDroppedOnComponent="onBindingDroppedOnComponent" :appData="appData" />
          </Panel>
        </div>
        <div class="panel logsPanel">
          <Panel title="Logs">
            <LogsPanel ref="logsPanel" :appData="appData"/>
          </Panel>
        </div>
      </div>
      <div class="rightBar">   
        <div class="panel bindingsPanel">
          <Panel title="Library & Bindings">
            <tabs mode="light">
              <tab title="UI Library" active="true">
                <LibraryPanel ref="libraryPanel" @blockCustomTriggered="onBlockCustomTriggered" />
              </tab>
              <tab title="Styles">
                <div id="myStyleEditorContainer"></div>
              </tab>
              <tab title="Bindings">
                <BindingsPanel ref="bindingsPanel" :appData="appData" @bindingOvered="onBindingOvered" />
              </tab>
            </tabs>
          </Panel>
        </div>
        <div class="panel propertiesPanel">
          <Panel :title="getPropertiesPanelName">
            <PropertiesPanel ref="propertiesPanel" :appData="appData" :selectedComponent="selectedComponent"/>
          </Panel>
        </div>  
        <div class="panel chartPropertiesPanel">
          <Panel title="Chart Properties">
            <ChartProperties ref="chartPropertiesPanel" :appData="appData" :selectedComponent="selectedComponent"/>
          </Panel>
        </div>  
        
      </div>
    </div>
    
    <BindingsConnector ref="bindingConnectorsManager" :appData="appData" />
    

  </div>
</template>

<script setup >

import axios from 'axios';

import _ from 'underscore';
import Panel from './Panel.vue'
import FilesPanel from './panels/FilesPanel.vue';
import Tabs from './tabs/Tabs.vue';
import Tab from './tabs/Tab.vue';

import GlobalEventsPanel from './panels/GlobalEventsPanel.vue'
import BindingsPanel from './panels/BindingsPanel.vue'
import StylesPanel from './panels/StylesPanel.vue'
import CanvasPanel from './panels/CanvasPanel.vue'
import LibraryPanel from './panels/LibraryPanel.vue'
import PropertiesPanel from './panels/PropertiesPanel.vue'
import ChartProperties from './ChartProperties.vue'
import LogsPanel from './panels/LogsPanel.vue'
import BindingsConnector from './BindingsConnector.vue'


import { computed, ref, onMounted } from 'vue'
import StatusMonitor from '../utils/StatusMonitor';
import HtmlTemplateParser from '../utils/HtmlTemplateParser';
import Configuration from '../utils/Configuration';

const logsPanel = ref(null);
const canvasPanel = ref(null);
const bindingsPanel = ref(null);
const libraryPanel = ref(null);
const propertiesPanel = ref(null);
const chartPropertiesPanel = ref(null);
const bindingConnectorsManager = ref(null);
const selectedComponent = ref(null);

let currentPage = null;

let appData = ref(null);
window.appData = appData;



// ==============================================================
//                  GB API Websocket
// ==============================================================
function onSocketOpen(data){
}

function onSocketMessage(data){
  let messageData = JSON.parse(data.data);
  // decode message if it's base64-encoded
  if( messageData.message && messageData.message.indexOf('base64:') == 0 ){
    messageData.message = atob( messageData.message.replace('base64:', '') );
  }
  if( logsPanel&& logsPanel.value){
    logsPanel.value.addLog( { appid: messageData.appid, timestamp: messageData.timestamp, source: 'server', level: messageData.type, message: messageData.message + ': ' + messageData.status } );
  }
}


const getPropertiesPanelName = computed( ()=>{
  let title = "Properties";
  if( selectedComponent && selectedComponent.value ){
    if( selectedComponent.value.attributes.name )
      title += ' - ' + selectedComponent.value.attributes.name;
  }else{
    title += ' (nothing selected)';
  }
  return title;
});

function onSocketClose(data){
  console.log( 'onSocketClose', data );
  statusMonitor.restart(5); // Attempt to restart with some delay
}

const statusMonitor = window.statusMonitor = new StatusMonitor( onSocketOpen, onSocketClose, onSocketMessage);



// ==============================================================
//                      URL PARAMS
// ==============================================================

// Get the URL params to configure connectivity with backend
// Expected url params: ?appid=1&apibase=http%3A%2F%2F127.0.0.1%3A10101%2Fapi%2Fv1%2F&CHANNEL__=JFKDAGKNJPPWPSQUIZHZXOBNVUYXZTAQ

const searchParams = getSearchParams();
let apibase = searchParams.apibase;
let appid = searchParams.appid;
let channel = searchParams.CHANNEL__;
let themeType = 'dark';
if( searchParams.theme == 'light' ){
  themeType = 'light';
}

window.apibase = apibase;
window.appid = appid;
window.channel = channel;

setTheme( themeType );

console.log( 'Setting API connection: apibase, appid, channel: ', apibase, appid, channel );

let urlParamsMissing = [];

if(!apibase ){
  urlParamsMissing.push( 'apibase' );
}
if( !appid){
  urlParamsMissing.push( 'appid' );
}
if( !channel){
  urlParamsMissing.push( 'channel' );
}

if( urlParamsMissing.length > 0 ){
  let urlParamsMessage = 'Missing URL params: ' + urlParamsMissing.join(', ');
  alert( urlParamsMessage );
  throw new Error( urlParamsMessage );
}

Configuration.channel = channel;



function onBlockCustomTriggered(props){
  bindingsPanel.value.setHandleBlocks(props); 
}


function saveTemplate(){

  const editedHtmlCss = editor.getCss();
  const entireHtml = editor.getHtml({ cleanId: true });
  const parser = new DOMParser();
  const doc = parser.parseFromString(entireHtml, 'text/html');
  const firstDiv = doc.body.querySelector('#gbTemplateContainer') || null;  
  
  
  HtmlTemplateParser.decodeAttributesInDOM(firstDiv );
  
  // On element attributes with value = "true" Grapes will return them empty (as it's the HTML standar)
  // This is breaking when it's a dynamic Vue attribute, since Vue requires a value to be passed
  // So we need to "fix" the HTML before saving it, by enforcing the "true" value to be present
  
  
  const firstDivContent = firstDiv.innerHTML;

  let beautifyOptions = {
      indent_size: 4,
      indent_char: " ",
      max_preserve_newlines: 5,
      preserve_newlines: true,
      keep_array_indentation: false,
      break_chained_methods: false,
      indent_scripts: "normal",
      brace_style: "collapse",
      space_before_conditional: true,
      unescape_strings: false,
      jslint_happy: false,
      end_with_newline: false,
      wrap_line_length: 0,
      indent_inner_html: false,
      comma_first: false,
      e4x: false,
      indent_empty_lines: false, 
      inline_custom_elements: false
    };

    const beautifiedHtml = html_beautify ( firstDivContent, beautifyOptions );

  

   let pageViewPath = currentPage.view;
   let encodedViewPath = encodeURIComponent(pageViewPath);
   let saveUrl = `${apibase}apps/${appid}/save?path=${encodedViewPath}`;
   
   
    axios.post( saveUrl, { payload: beautifiedHtml } )
        .then(  (response) => {
          
          let saveCssUrl = `${apibase}apps/${appid}/save?path=public/css/autogenerated.css`;
          axios.post( saveCssUrl, { payload: editedHtmlCss } )
            .then(  (response) => {
            } )
            .catch( (error) => {
              console.log( "ERROR saving css file response: ", error );
            });
        } )
        .catch( (error) => {
          console.log( "ERROR saving template file response: ", error );
        });
}


  function getSearchParams() {
    const params = new URLSearchParams(window.location.search);
    const obj = {};
    for (const [key, value] of params.entries()) {
      obj[key] = value;
    }
    return obj;
  } 


  function onBindingOvered(event){

    // Inform connectors-manager about mouse-overed binding
    bindingConnectorsManager.value.onBindingOvered( event );

    // Ask grapes iframe for list of elements using overed binding
    let iframeElement = document.querySelector('.gjs-frame') ;
    if( event.binding ){
      let message = { 
        target:"main-controller", 
        payload: { 
          command:"reportElementsUsingBinding", 
          bindingName:event.binding.name 
        }
      };
      iframeElement.contentWindow.postMessage(message, "*");
    }else{
      bindingConnectorsManager.value.updateElementsPositions( [] );
    }
  }

  function onComponentSelected(com){
    window.selectedComponent = selectedComponent.value = com;
    
    if( propertiesPanel.value){
      propertiesPanel.value.setSelectedComponent(com);
      
    }
    if( chartPropertiesPanel.value){
      chartPropertiesPanel.value.setSelectedComponent(com);
    }
  }

  function onBindingDroppedOnComponent( data ){
    if( chartPropertiesPanel.value){
      chartPropertiesPanel.value.assignNewDataSource(data);
    }
  }

  function updateData(newData){
    appData.value = (newData);
  }

  function valueIsModelField( value ){
      if( currentPage == null )
        return false;
      let modelFields = currentPage.model.fields;
      let modelFieldNames = modelFields.map( (field)=>{ return field.name } );
      if( modelFieldNames.indexOf(value) > -1 || value == '' )
        return true;
      else
        return false;
    }

    window.valueIsModelField = valueIsModelField;

  function translateBindingTypes(pages){
    const typesMap = {
                "{Function}": "Function", 
                "{Bool}": "Boolean", 
                "{String}": "String", 
                "{Char}": "String", 
                "{Date}": "Date", 
                "{Number}": "Number", 
                    "{Int64}": "Number", 
                    "{Int32}": "Number", 
                    "{Int16}": "Number", 
                    "{Int8}": "Number",
                    "{Float64}": "Number",
                    "{Float32}": "Number",
                    "{Float16}": "Number",
                    "{Float8}": "Number",
                "{Vector}": "Array",
                    "{Vector{Bool}}": "Array",
                    "{Vector{String}}": "Array",
                    "{Vector{Char}}": "Array",
                    "{Vector{Number}}": "Array",
                    "{Vector{Int64}}": "Array",
                    "{Vector{Int32}}": "Array",
                    "{Vector{Int16}}": "Array",
                    "{Vector{Int8}}": "Array", 
                "{Object}": "Object", 
                "{NamedTuple{(:min, :max), Tuple{Int64, Int64}}}": "Object", 
                "{Dict}": "Object", 
                "{RangeData{Int64}}": "Number", 
                "{Vector{PlotlyBase.GenericTrace{Dict{Symbol, Any}}}}": "PlotlyBase.GenericTrace", 
                "{PlotlyBase.Layout{Dict{Symbol, Any}}}": "PlotlyBase.Layout", 
                "{DataTable{DataFrames.DataFrame}}": "DataTable.DataFrames.DataFrame", 
                "{DataFrames.DataFrame}": "DataFrames.DataFrame", 
                "{PlotlyData}": "PlotlyData", 
                "{DataTablePagination}": "DataTablePagination", 
            };
    pages.forEach( (page)=>{
      // remove non-reactive vars
      page.model.fields = page.model.fields.filter( (field)=>{ return field.isreactive} );
      // translate types
      page.model.fields.forEach( (field)=>{
        let fieldType = field.type;
        let cleanType = fieldType.replace("Reactive", "");
        let mappedType = typesMap[cleanType];
        if( mappedType ){
          field.type = mappedType;
        }
      } );
      
      // sort alphabetically
      page.model.fields = _.sortBy( page.model.fields, (field)=>{ return field.name.toLowerCase() } );
    } );
  }

  

  onMounted( ()=>{
    init();
  } );

  function init(){
    window.canvasPanel = canvasPanel;
    // listen for scrolling to update iframe position
    document.addEventListener('scroll', e => {
      let bindConnMgr = bindingConnectorsManager.value;
      if( bindConnMgr == null )
        return;
      bindConnMgr.updateIframePosition();
    });
      
    // Listen fot postMessge events from grapes iframe
    window.addEventListener(
      "message",
      (event) => {
        if (event.data.target !== "nocode-builder") return;

        let bindConnMgr = bindingConnectorsManager.value;
        if( bindConnMgr == null )
          return;

        bindConnMgr.updateElementsPositions(event.data.payload);
      },
      false,
    );

    if(!apibase || !appid ){
      return;
    }

    // Load the list of apps
    axios.get( apibase + 'apps' )
    .then( (result) => {

      // Fidn the requested app
      let myApp = result.data.applications.find( (app)=>{ 
        return app.id.value.toString() == appid 
      } );
      // Store the general data about the app (as returned by /apps endpoint)
      //updateData(myApp);
      let fullAppData = myApp;

      if( myApp != null ){
        // Load the app data from API
        axios.get( apibase + 'apps/' + appid + '/pages'  )
        .then( (result) => {
          translateBindingTypes(result.data.pages);
          fullAppData.pages = result.data.pages;
          updateData(fullAppData);
          setTimeout( ()=>{
            currentPage = result.data.pages[0];
            let readTemplateFilePAth = apibase + 'apps/' + appid + '/edit?path=' + encodeURIComponent( currentPage.view );
            canvasPanel.value.loadTemplate( readTemplateFilePAth );
          }, 1000 );
        }).catch( (error) => {
          console.log('error', error);
        });
      }
      
    }).catch( (error) => {
      console.log('error', error);
    });

    statusMonitor.connect();

  }


</script>

<style scoped lang="scss">
.topBar{
  padding: 5px;
  height: 40px;
  display: flex;
  flex-direction: row;
  width: 100%;
}
.topToolBar{
  width: 100px;
  text-align: right;
  padding-right: 5px;
}
.leftBar{
  flex-grow: 1;
  max-width:300px;
}
.centralBar{
  flex-grow: 3;
  display: flex;
  flex-direction: column;
  border-right: solid 1px #00000050;
  width: calc(100% - 300px);
}
.rightBar{
  flex-grow: 1;
  width: 300px;
  max-height: calc(100% - 0px);
  overflow-y: auto;
}
.panel{
  margin: 0px;
  border-top: solid 1px #00000050;
}

.mainContainer{
  display: flex;
  flex-direction: column;
  height: 100%;
  background-color: var(--background);
}
.columnStack{
  display: flex;
  flex-direction: column;
}
.rowStack{
  display: flex;
  flex-direction: row;
  flex: 1;
  height: calc(100% - 40px);
}

.canvasPanel{
  height: 100%;
}


</style>
